@font-face {
  font-family: "Lato";
  src: url("../../fonts/Lato-Heavy.ttf") format("truetype"), url("../../fonts/Lato-Heavy.woff") format("woff");
  font-weight: 900;
  font-style: normal;
}

@font-face {
  font-family: "Lato";
  src: url("../../fonts/Lato-Regular.ttf") format("truetype"), url("../../fonts/Lato-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Lato";
  src: url("../../fonts/Lato-Bold.ttf") format("truetype"), url("../../fonts/Lato-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Lato";
  src: url("../../fonts/Lato-Semibold.ttf") format("truetype"), url("../../fonts/Lato-Semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: "Raleway-ExtraBold";
  src: url("../../fonts/Raleway-ExtraBold.ttf") format("truetype"), url("../../fonts/Raleway-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
}

/**********************
* Base style
**********************/
html,
body {
  height: 100%;
}

body {
  background: #fff;
  font-family: 'Lato', sans-serif;
  font-size: 10px;
  color: #000;
  font-weight: 400;
  min-width: 320px;
}

img {
  max-width: 100%;
  height: auto;
}

section {
  position: relative;
}

ul {
  list-style: none;
  padding: 0;
}

br {
  font-size: 0;
}

*,
*:after,
*:before {
  box-sizing: border-box;
}

.container {
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
}

input {
  outline: none;
}

/**********************
* typography
**********************/
h1,
h2,
h3,
h4,
h5,
h6,
p,
hr {
  margin: 0;
  font-weight: 400;
}

h1 {
  font-family: 'Lato', sans-serif;
  font-weight: 700;
}

h2 {
  font-family: Lato, sans-serif;
  font-weight: 900;
}

h3 {
  font-family: 'Raleway-ExtraBold', sans-serif;
  font-weight: 800;
}

a {
  display: inline-block;
  text-decoration: none;
}

.navbar {
  position: fixed;
  z-index: 100;
  width: 100%;
  background-color: rgba(16, 16, 29, 0.9);
  height: 100px;
}

.navbar__wrap {
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  position: relative;
}

.navbar__name {
  font-size: 28px;
  line-height: 18px;
  text-transform: uppercase;
  color: #fff;
}

.navbar__name_blue {
  color: #42a9ed;
}

.navbar__list {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  max-width: 433px;
  width: 100%;
  margin: 0 0 0 50px;
}

.navbar__list.active {
  display: flex;
  position: absolute;
  right: 0;
  top: 100px;
  flex-direction: column;
  background-color: rgba(61, 64, 80, 0.95);
  max-width: 576px;
  width: 100%;
}

.navbar__list.active .navbar__item {
  width: 100%;
  height: 100%;
  text-align: center;
}

.navbar__list.active .navbar__link {
  width: 100%;
  height: 100%;
  padding: 15px 0;
}

.navbar__list.active .navbar__link:hover {
  background-color: #3d4050;
}

.navbar__item:last-child {
  margin-right: 0;
}

.navbar__link {
  font-family: Lato, sans-serif;
  font-weight: 600;
  font-size: 13px;
  color: #fff;
  text-transform: uppercase;
  transition: all .3s;
  padding: 10px 5px;
}

.navbar__link:hover {
  color: #43abf0;
}

.navbar__burger {
  display: none;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 40px;
  cursor: pointer;
  padding: 5px;
  transition: all .1s;
}

.navbar__burger span {
  height: 2px;
  width: 30px;
  background-color: #fff;
}

.navbar__burger.active {
  transform: rotate(-90deg);
}

.header {
  background: url("../img/headerBg.png") top no-repeat;
  background-size: cover;
  min-height: 500px;
  padding-top: 240px;
  padding-bottom: 190px;
}

.header__title {
  font-size: 48px;
  line-height: 64px;
  color: #000;
  text-transform: uppercase;
  margin-bottom: 50px;
}

.header__title_blue {
  color: #3e9cdc;
}

.header__subtitle {
  font-family: Lato, sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 34px;
  color: #000;
}

.header__list {
  margin-bottom: 70px;
  margin-top: 0;
}

.header__item {
  position: relative;
  font-family: Lato, sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 34px;
  color: #000;
  padding-left: 10px;
}

.header__item:before {
  content: '-';
  position: absolute;
  left: 0;
}

.header__buttons-group {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 393px;
  width: 100%;
}

.header__button {
  height: 42px;
  border: 1px solid #000;
  border-radius: 3px;
  font-family: Lato, sans-serif;
  font-weight: 600;
  font-size: 15px;
  line-height: 42px;
  text-transform: uppercase;
  width: 182px;
  background-color: #fff;
  cursor: pointer;
  transition: all .3s;
}

.header__button:hover {
  background-color: #3e9cdc;
  color: #fff;
  border-color: #fff;
}

.content {
  padding-top: 120px;
  padding-bottom: 148px;
  background: #272733 url("../img/content-bg-map.png") center no-repeat;
}

.content__title {
  font-size: 36px;
  margin-bottom: 20px;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
}

.content__title_blue {
  color: #3e9cdc;
}

.content__subtitle {
  font-size: 18px;
  line-height: 22px;
  color: #bbbbbb;
  margin-bottom: 70px;
  text-align: center;
}

.content__scheme {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 75px;
}

.scheme {
  position: relative;
  z-index: 1;
  background-repeat: no-repeat;
  background-position: 48% 30%;
}

.scheme__step {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
}

.scheme__step:last-child .scheme__img:after {
  display: none;
}

.scheme__img {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #3e9cdc;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border: 5px solid #286690;
  margin-bottom: 20px;
}

.scheme__img:after {
  content: url("../img/dot.png");
  position: absolute;
  left: calc(100% + 25px);
  top: calc(50% - 6px);
}

.scheme__desc {
  font-size: 16px;
  line-height: 20px;
  color: #fff;
  text-align: center;
  padding: 0 8px;
}

.form {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.form__select {
  flex-grow: 1;
  font-size: 18px;
  color: #272733;
  padding: 15px;
  border: 1px solid #fff;
  border-radius: 3px;
  background-color: #dfdfe1;
  position: relative;
  box-sizing: border-box;
  cursor: pointer;
}

.form__input {
  flex-grow: 1;
  font-size: 18px;
  color: #272733;
  padding: 15px;
  border: 1px solid #fff;
  border-radius: 3px;
  background-color: #dfdfe1;
  margin-left: 30px;
  align-self: flex-start;
  height: 48px;
}

.form__addfile {
  flex-grow: 1;
  flex-basis: 31%;
  background-color: #f4f4f5;
  height: 48px;
  border-radius: 3px;
  cursor: pointer;
  border: 2px solid #f4f4f5;
  position: relative;
  display: flex;
}

.form__addfile:hover {
  border: 2px solid #3e9cdc;
}

.form__addfile input {
  display: none;
}

.form__addfile-text {
  margin: auto;
  line-height: 48px;
  color: #272733;
  font-size: 15px;
  font-weight: 600;
  text-transform: uppercase;
  position: relative;
  padding-left: 35px;
}

.form__addfile-text:before {
  content: url("../img/add.png");
  position: absolute;
  left: 0;
  top: 3px;
}

.form__submit {
  position: relative;
  left: -10px;
  height: 48px;
  font-size: 15px;
  font-weight: 600;
  line-height: 48px;
  text-align: center;
  border-radius: 3px;
  border: 1px solid #3e9cdc;
  max-width: 32%;
  min-width: 120px;
  width: 100%;
  margin: 0 auto;
  text-transform: uppercase;
  background-color: #3e9cdc;
  color: #fff;
  cursor: pointer;
}

.form__submit:hover {
  border: 1px solid #f4f4f5;
}

.form__add {
  visibility: hidden;
  position: absolute;
}

.select {
  margin-bottom: 30px;
  height: 48px;
  line-height: 1;
}

.select__header {
  position: relative;
}

.select__header::after {
  content: '\005E';
  font-size: 26px;
  position: absolute;
  right: 0;
  color: #272733;
  transform: rotate(180deg);
  top: -10px;
}

.select__list {
  flex-direction: column;
  display: flex;
  max-height: 180px;
  overflow-y: auto;
}

.select__list::-webkit-scrollbar {
  width: 7px;
}

.select__list::-webkit-scrollbar-track {
  background-color: #3d4050;
}

.select__list::-webkit-scrollbar-thumb {
  background-color: #3e9cdc;
  outline: 1px solid slategrey;
  border-radius: 5px;
}

.select__list::-webkit-scrollbar {
  width: 7px;
}

.select__list::-webkit-scrollbar-track {
  background-color: #3d4050;
}

.select__list::-webkit-scrollbar-thumb {
  background-color: #3e9cdc;
  outline: 1px solid slategrey;
  border-radius: 5px;
}

.select__list__wrapper {
  display: none;
  top: -1px;
  left: -1px;
  background-color: rgba(39, 39, 51, 0.9);
  width: calc(100% + 2px);
  border-radius: 3px;
  position: absolute;
}

.select__list__wrapper.active {
  display: block;
  border: 1px solid #3d4050;
}

.select__item {
  padding: 9px 15px;
  line-height: 1;
  color: #ffffff;
  cursor: pointer;
}

.select__item:hover {
  color: #42a9ed;
}

.select__item.title {
  background-color: #3d4050;
  color: #fff;
  height: 48px;
  padding: 15px;
  position: relative;
}

.select__item.title::after {
  content: '\005E';
  font-size: 22px;
  position: absolute;
  right: 15px;
  color: #fff;
  transform: rotate(180deg);
  top: 7px;
}

.slide {
  margin-right: 30px;
  margin-bottom: 50px;
  flex-basis: 65%;
}

.slide__title, .slide__value {
  font-size: 18px;
  line-height: 22px;
  color: #fff;
  margin-bottom: 20px;
}

.slide__wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.slider {
  -webkit-appearance: none;
  width: 100%;
  padding: 0;
  position: relative;
  cursor: pointer;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 24px;
  height: 24px;
  border: 3px solid #286690;
  border-radius: 50%;
  background: #42a9ed;
  cursor: pointer;
  margin-top: -10px;
}

.slider::-webkit-slider-runnable-track {
  width: 100%;
  height: 6px;
  cursor: pointer;
  background: #d3d3d3;
  border-radius: 2px;
  border: 1px solid #fff;
}

.slider::-moz-range-thumb {
  width: 24px;
  height: 24px;
  border: 3px solid #286690;
  border-radius: 50%;
  background: #42a9ed;
  cursor: pointer;
}

.slider::-moz-range-track {
  width: 100%;
  height: 6px;
  cursor: pointer;
  background: #d3d3d3;
  border-radius: 2px;
  border: 1px solid #fff;
}

.slider::-ms-track {
  margin-top: -12px;
  width: 100%;
  height: 6px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
  padding: 12px 0;
}

.slider::-ms-fill-lower {
  background: #d3d3d3;
  border: 1px solid #fff;
  border-radius: 2px;
  cursor: pointer;
}

.slider::-ms-fill-upper {
  background: #d3d3d3;
  border: 1px solid #fff;
  border-radius: 2px;
  cursor: pointer;
}

.slider::-ms-thumb {
  width: 24px;
  height: 24px;
  border: 3px solid #286690;
  border-radius: 50%;
  background: #42a9ed;
}

.footer {
  background-color: #3d4050;
  height: 80px;
  display: flex;
  align-items: center;
}

.footer__container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__group {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__group_center {
  max-width: 380px;
  width: 100%;
}

.footer__group_center .footer__link {
  color: #c3c3c3;
}

.footer__group_right {
  max-width: 283px;
  width: 100%;
}

.footer__group_right .footer__link {
  color: #fff;
}

.footer__copyright {
  font-size: 13px;
  color: #fff;
  min-width: 307px;
}

.footer__line {
  margin: 0 10px;
}

.footer__link {
  font-size: 13px;
  padding-left: 25px;
  position: relative;
}

.footer__icon {
  position: absolute;
  left: 0;
  top: -1px;
}

.footer__line {
  color: #c3c3c3;
  font-size: 18px;
}

/**********************
* MEDIA
**********************/
/* Large Devices, Wide Screens */
@media only screen and (max-width: 1200px) {
  .header {
    padding-top: 200px;
    padding-bottom: 150px;
  }
}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {
  .scheme__img {
    margin: 0 auto 20px;
  }
  .scheme__img:after {
    left: calc(100% + 15px);
  }
  .form__select,
  .form__input,
  .form__addfile {
    order: 1;
    flex-basis: 47%;
  }
  .form__input {
    margin-left: 0;
    margin-bottom: 20px;
  }
  .form__addfile {
    margin-left: 20px;
  }
  .form__select {
    margin: 0 20px 20px 0;
  }
  .form__slide {
    order: 2;
    flex-basis: 100%;
    margin-right: 0;
    margin-bottom: 30px;
  }
  .slide__wrap {
    max-width: 640px;
    margin: 0 auto;
  }
  .form__submit {
    order: 3;
  }
  .footer__group {
    flex-direction: column;
    width: auto;
  }
  .footer__link {
    margin-bottom: 10px;
  }
  .footer__link:last-child {
    margin-bottom: 0;
  }
}

/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  .navbar__list {
    display: none;
  }
  .navbar__burger {
    display: flex;
  }
  .header__container {
    text-align: center;
  }
  .header__title {
    font-size: 34px;
    margin-bottom: 40px;
  }
  .header__list {
    display: inline-block;
    margin-bottom: 40px;
  }
  .header__item {
    text-align: start;
  }
  .header__buttons-group {
    margin: 0 auto;
  }
  .content {
    padding-top: 100px;
    padding-bottom: 100px;
  }
  .content__title {
    font-size: 28px;
  }
  .content__subtitle {
    margin-bottom: 50px;
  }
  .scheme {
    flex-wrap: wrap;
    justify-content: center;
  }
  .scheme__step {
    flex-basis: 33%;
    margin-bottom: 20px;
  }
  .scheme__img {
    margin: 0 auto 20px;
  }
  .scheme__img:after {
    display: none;
  }
  .form__select,
  .form__input,
  .form__addfile {
    height: 36px;
    font-size: 16px;
  }
  .form__addfile-text {
    font-size: 13px;
    line-height: 36px;
  }
  .form__select {
    padding: 10px 15px;
  }
  .slide__title {
    font-size: 16px;
  }
  .form__submit {
    height: 36px;
    font-size: 13px;
    line-height: 36px;
  }
  .footer {
    height: auto;
    padding: 15px 0;
  }
  .footer__container {
    flex-wrap: wrap;
    justify-content: center;
  }
  .footer__copyright {
    order: 5;
  }
  .footer__group {
    order: 1;
    width: 50%;
    margin-bottom: 20px;
  }
  .footer__line {
    display: none;
  }
}

/* Small Devices, Phones */
@media only screen and (max-width: 576px) {
  .navbar {
    height: 80px;
  }
  .navbar__list.active {
    top: 80px;
  }
  .header__button {
    height: 30px;
    font-size: 14px;
    width: 140px;
    line-height: 30px;
  }
  .content {
    padding-top: 80px;
    padding-bottom: 60px;
  }
  .content__title {
    font-size: 24px;
  }
  .scheme {
    margin-bottom: 40px;
  }
  .scheme__step {
    margin-bottom: 20px;
  }
  .scheme__img {
    height: 80px;
    width: 80px;
  }
  .scheme__desc {
    font-size: 14px;
    padding: 0 4px;
  }
  .header {
    padding-top: 150px;
    padding-bottom: 100px;
  }
  .header__title {
    font-size: 28px;
    line-height: 48px;
  }
  .form__select,
  .form__input,
  .form__addfile,
  .slide__title {
    font-size: 14px;
  }
  .form__addfile-text,
  .form__submit {
    font-size: 12px;
  }
  .slide__value {
    font-size: 14px;
  }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) {
  .navbar__name {
    font-size: 20px;
  }
  .header {
    padding-top: 130px;
    padding-bottom: 40px;
  }
  .header__title {
    line-height: 36px;
    margin-bottom: 25px;
  }
  .header__subtitle,
  .header__item {
    font-size: 16px;
  }
  .header__list {
    margin-bottom: 20px;
  }
  .content {
    padding-top: 60px;
    padding-bottom: 40px;
  }
  .content__subtitle {
    margin-bottom: 30px;
  }
  .content__scheme {
    margin-bottom: 30px;
  }
  .scheme__step {
    flex-basis: 50%;
  }
  .scheme__img {
    margin-bottom: 10px;
  }
  .header__title {
    font-size: 28px;
  }
  .header__buttons-group {
    flex-direction: column;
    align-items: center;
  }
  .header__button:nth-child(1) {
    margin-bottom: 10px;
  }
  .form__select,
  .form__input,
  .form__addfile,
  .form__slide,
  .form__submit {
    order: 1;
    flex-basis: 100%;
  }
  .form__addfile {
    margin: 0 0 20px 0;
  }
  .form__select {
    margin-right: 0;
  }
}
